import React, { useState, useEffect } from "react";
import Quagga from "quagga";

const PaymentForm = () => {
  const [ticketNumber, setTicketNumber] = useState('');
  const [amount, setAmount] = useState('');
  const [paymentResult, setPaymentResult] = useState({});

  const Scanner = () => {
    const [ticketNumberFromScanner, setTicketNumberFromScanner] = useState("");
  
    useEffect(() => {
      console.log('useeffect');
      Quagga.init({
        inputStream: {
          type: "Live",
          constraints: {
            width: 640,
            height: 480,
            facingMode: "environment"
          }
        },
        locator: {
          patchSize: "medium",
          halfSample: true
        },
        numOfWorkers: 2,
        decoder: {
          readers: ["code_128_reader"]
        },
        locate: true
      }, function(err) {
        if (err) {
          console.log(err);
          return
        }
        Quagga.start();
      });
  
      Quagga.onDetected(function(result) {
        setTicketNumberFromScanner(result.codeResult.code);
        Quagga.stop();
      });
  
      return () => {
        Quagga.stop();
      };
    }, []);
  
    return (
      <div>
        <p>Número do Ticket: {ticketNumberFromScanner}</p>
      </div>
    );
  };

  const handleSubmit = async (event) => {
    event.preventDefault();

    // Envia uma solicitação para o servidor para processar o pagamento
      await fetch('http://localhost:3005/api/pay-ticket', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ ticketNumber, amount })
    })
      .then((response) => response.json())
      .then((result) => {
        setPaymentResult(result);
        console.log(result);
      });
      console.log('funcionou');
  };
  
  return (
    <>
      <form className="payment-form" onSubmit={handleSubmit}>
      <h2>Pagar Ticket</h2>
      <div className="form-group">
        <label htmlFor="ticketNumber">Número do Ticket:</label>
        <Scanner />
        <input
          type="text"
          id="ticketNumber"
          name="ticketNumber"
          value={ticketNumber}
          onChange={(event) => setTicketNumber(event.target.value)}
        />
      </div>
      <div className="form-group">
        <label htmlFor="amount">Valor a Pagar:</label>
        <input
          type="number"
          id="amount"
          name="amount"
          value={amount}
          onChange=  {(event) => setAmount(event.target.value)}

        />
      </div>
      <button type="submit">Pagar</button>
      {paymentResult ? (
        <p>{paymentResult.message}</p>
      ) : null}
    </form>
    </>
  );
};

export default PaymentForm;